<template>
  <div class="component homeSection1">
    <figure
      class="theme image layout"
      :style="{'background-image': 'url(' + image + ')'}"
    ></figure>
    <div class="headline layout">
      <h2 class="title deco" data-title="LINEN BLAZER">
        LINEN BLAZER
      </h2>
    </div>
    <i class="deco lt"></i>
  </div>
</template>

<script>
import image from '@/assets/images/pdt1.jpg'

export default {
  data () {
    return {
      image
    }
  }
}
</script>

<style lang="scss">
.component.homeSection1{
  position: relative;
  margin-top: 30%;
  .layout{
    position: relative;
    z-index: 9;
  }
  .headline{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9;
  }
  figure.theme.image{
    display: block;
    width: 100%;
    height: 0;
    padding-bottom: 80%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  i.deco{
    &.lt{
      position: absolute;
      top: 0;
      right: 0;
      display: block;
      width: 40%;
      height: 0;
      padding-bottom: 28%;
      border: 1em solid default(color);
      transform: translateX(20%) translateY(-40%);
    }
  }
}
</style>



