<template>
  <div id="app">
    <theHeader />
    <main class="main">
      <router-view />
    </main>
    <theFooter />
  </div>
</template>

<script>
import theHeader from '@/components/theHeader'
import theFooter from '@/components/theFooter'

export default {
  name: 'App',
  components: {
    theHeader,
    theFooter
  }
}
</script>

<style lang="scss">
@import './assets/sass/main.scss';

html, body , #app{
  height: 100%;
  padding: 0;
  margin: 0;
}
#app{
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: table;
  table-layout:fixed;
  header.header, footer.footer, main.main{
    display: table-row;
    width: 100%;
  }
  header.header, footer,footer{
    height: 1px;
  }

  background: rgba(0,0,0,.05);
  main.main{
    height: auto;
    .container{
      padding-top: 1.25em;
      padding-bottom: 1.25em;
    }
  }
}

// grid system
.container-grid{
  display: grid; //setup grid
  height: 100%;
  margin: 0 auto;

// // stystem1
//   grid-template-columns: repeat(1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr); // grid width

//   // grid-gap: 20px 10px; // up-down left-right spacing
//   grid-column-gap: 10px; // up down spacing
//   grid-row-gap: 20px; // left right spacing

//   grid-template-rows: 100px 200px;// grid height every column

//   .item{
//     &:nth-child(1){}
//   }

// system2
  grid-gap: 0;
  @include breakpoint-u(960px){
    width: 960px;
    grid-template: repeat(21, [row] minmax(100px, 1fr)) / repeat(8, [col] 120px); // 4 spaces in 1 col, eight column and 120 height.
    grid-template-areas:
      "a a a a a a a a"
      "b b b b b . . ."
      "b b b b b . . ."
      "b b b b b c c ."
      "b b b b b c c ."
      "b b b b b . . ."
      "b b b b b . . ."
      "d d d d . . . ."
      "d d d d . e e e"
      "d d d d . e e e"
      "d d d d . e e e"
      ". . . . . e e e"
      "f f f f . . . ."
      "f f f f . . . ."
      ". . . . g g g g"
      ". . . . g g g g"
      "h h . . g g g g"
      "h h . . g g g g"
      "h h . . . . . ."
      "h h . . . . . ."
    ;
  }
  @include breakpoint-d(960px){
    width: 480px;
    grid-template: repeat(27, [row] minmax(100px, 1fr)) / repeat(4, [col] 120px);
    grid-template-areas:
    "a a a a"
    "b b b b"
    "b b b b"
    "b b b b"
    ". . . ."
    "c c c ."
    "c c c ."
    ". . . ."
    ". d d d"
    ". d d d"
    ". d d d"
    ". . . ."
    "e e e e"
    "e e e e"
    "e e e e"
    ". . . ."
    "f f f f"
    "f f f f"
    ". . . ."
    ". . . ."
    "g g g ."
    "g g g ."
    ". . . ."
    ". . . ."
    "h h h ."
    "h h h ."
  }
  .grid-item{
    // border: 1px solid red;
    $loop: a, b, c, d, e, f, g, h;
    @each $now in $loop{
      $i: index($loop, $now);
      &:nth-child(#{$i}){
        grid-area: nth($loop, $i);
      }
    }
  }
}
</style>



