import Vue from 'vue'
import Router from 'vue-router'

import appHome from '@/views/appHome'
import appProduct from '@/views/appProduct'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: appHome,
      children: [
        {
          path: 'product',
          component: appProduct
        }
      ]
    }
  ]
})



